import React from "react";
import { Auth } from "@supabase/auth-ui-react";
import { supabase } from "../../supabase/supabase";

export default function Autho() {
  return (
    <div>
      <Auth
        supabaseClient={supabase}
        // Puedes personalizar el aspecto y el comportamiento del componente Auth
        // Por ejemplo, puedes cambiar el tema, los idiomas, los proveedores, etc.
        // Consulta la documentacin para ms detalles[^1^][4]
        appearance={{
          theme: "dark",
          languages: ["es"],
        }}
      />
    </div>
  );
}
