import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { supabase } from "../../supabase/supabase";
import { useNavigate } from "react-router-dom";

export default function Login({}) {
  //
  //Agendar un codigo de cockee una vez echas las llamadas a la base de iles
  //

  const [datos, setdatos] = useState({ correo: "", contrasea: "" });
  const [Users, setUsers] = useState();
  const [visible, setvisible] = useState(false);
  const [type, settype] = useState("password");

  useEffect(() => {
    const fetchBebidas = async () => {
      const { data: User, error } = await supabase.from("User").select();
      setUsers(User);
    };
    fetchBebidas();
  }, [Users]);
  console.log(Users);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  // Manejamos el envo del formulario
  function onSubmit(dat) {
    // Aqu comparamos el login del usuario
    setdatos(dat);
    Users.map((m) => {
      if (datos.correo == m.correo && datos.contrasea == m.contrasea) {
        console.log("login");
      }
    });
  }

  function Password() {
    const navigate = useNavigate();
    navigate("Gym1/recuperar-contrasea");
  }
  function viewPass() {
    if (!visible) {
      setvisible(true);
      settype("text");
    } else {
      setvisible(false);
      settype("password");
    }
  }
  return (
    <div className="formulario">
      <h1 className="formulario-titulo">Login</h1>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="grande">
          <div className="flex">
            <div className="password">
              <input
                id="correo"
                type="email"
                placeholder="Correo..."
                {...register("correo", {
                  required: true,
                  pattern: /^\S+@\S+$/i,
                })}
              />
              <ion-icon name="mail-outline"></ion-icon>
            </div>
            {errors.correo && (
              <span>Este campo es obligatorio y debe ser un email vlido</span>
            )}
          </div>
        </div>

        <div className="grande">
          <div className="flex">
            <div className="password">
              <input
                id="contrasea"
                type={type}
                placeholder="Contrasea..."
                {...register("contrasea", { required: true, minLength: 6 })}
              />{" "}
              <a onClick={viewPass}>
                {visible ? (
                  <ion-icon name="eye-outline"></ion-icon>
                ) : (
                  <ion-icon name="eye-off-outline"></ion-icon>
                )}
              </a>
            </div>
            {errors.contrasea && (
              <span>
                Este campo es obligatorio y debe tener al menos 6 caracteres
              </span>
            )}
          </div>
        </div>

        <button className="price__btn" type="submit">
          Enviar
        </button>
      </form>
      <button className="price__btn1" onClick={Password}>
        <h5>A olvidado su contrasea?</h5>
      </button>
    </div>
  );
}
