import { useState } from "react";
import "./barnav.css";
import logo from "./logoK.jpg";
import Menu from "./MenuBar/MenuBar";

export default function Barnav() {
  const [active, setactive] = useState();

  const fecha = new Date();
  var semana = fecha.getDay();
  var hora = fecha.getHours();
  var OpenClose = null;
  var colores;
  if (semana == 1 || semana == 2 || semana == 3 || semana == 4) {
    if (hora >= 9 && hora <= 23) {
      OpenClose = "Abierto";
    } else {
      OpenClose = "Cerrado";
    }
  } else if (semana == 5) {
    if (hora >= 9 && hora <= 13) {
      OpenClose = "Abierto";
    } else {
      OpenClose = "Cerrado";
    }
  } else {
    OpenClose = "Cerrado";
  }
  function Profile() {
    if (OpenClose == "Abierto") {
      colores = " #994f09";
    } else if (OpenClose == "Cerrado") {
      colores = " red";
    }

    return <h4 style={{ color: colores }}>{OpenClose}</h4>;
  }
  return (
    <nav class="barnav">
      <h2>Caf Eclipse</h2>

      <div id="hora">
        <Profile />
      </div>
    </nav>
  );
}
