import { useEffect, useState } from "react";
import React from "react";
import Barnav from "./Components/Barnav-sup/barnav";
import Body from "./Components/Body/body";
import "./style.css";
import Footer from "./Components/Footer/footer";
import Login from "./Components/Login/login";
import Autho from "./Components/Login/Auth";
import { Route, Routes, useNavigate } from "react-router-dom";

function App() {
  function day() {
    const root = document.documentElement;
    root.style.setProperty("--primary-color", "#994f09");
    root.style.setProperty("--second-color", "#e7e0e0");
    root.style.setProperty("--background-color", "#f9f5f0");
    root.style.setProperty("--font-color", "#151515");
    root.style.setProperty("--font2-color", "#414040");
  }
  function nigth() {
    const root = document.documentElement;
    root.style.setProperty("--primary-color", "#d37014");
    root.style.setProperty("--second-color", "#302f2f");
    root.style.setProperty("--background-color", "#292827");
    root.style.setProperty("--font-color", "#ffffff");
    root.style.setProperty("--font2-color", "#cecece");
  }
  const fecha = new Date();
  var hora = fecha.getHours();
  if (hora >= 7 && hora <= 18) {
    day();
  } else if (hora >= 19 || hora <= 6) {
    nigth();
  }

  return (
    <div>
      <div class="container" className="App">
        <Barnav />
        <Routes>
          <Route
            path="Cafe/*"
            element={
              <>
                <Body />
              </>
            }
          >
            <Route path="login/" element={<Login />} />
            <Route path="Auth/" element={<Autho />} />
          </Route>
        </Routes>
        <Footer />
      </div>
    </div>
  );
}

export default App;
