import { useState } from "react";

export default function SlideFav({ favGlobal }) {
  const fav = favGlobal;
  return (
    <div id="nuevo">
      <section id="sece">
        <div class="naveg">
          <div className="hrv"></div>
          <h2>Especiales</h2>
          <div className="hrv"></div>
        </div>
        <div id="nave" className="carrusel">
          {fav.map((objeto) => (
            <div data-aos="flip-down" class="card-product2" id={objeto.id}>
              <div class="container-img2">
                <div class="sub-cont fullpc">
                  <p class="price">${objeto.price}.00</p>
                </div>

                <div class="caja">
                  <h3>{objeto.title}</h3>

                  <div class="star">
                    {Array.from({ length: objeto.star }, (_, i) => (
                      <ion-icon name="star"></ion-icon>
                    ))}
                  </div>
                </div>
                <img src={objeto.image} />
              </div>
            </div>
          ))}
        </div>
        <div className="circles"></div>
      </section>
    </div>
  );
}
