import React from "react";
import { useState, useEffect } from "react";
import { useSwipeable } from "react-swipeable";
import "./menuBar.css";
import { supabase } from "../../../supabase/supabase";
import { useNavigate } from "react-router-dom";

export default function Menu({ hhh }) {
  const [menuVisible, setMenuVisible] = useState();
  const [objetosDif, setobjetosDif] = useState([]);

  useEffect(() => {
    setMenuVisible(hhh);
  }, [hhh]);

  const handleDrag = () => {
    setMenuVisible(false);
  };

  useEffect(() => {
    const fetchBebidas = async () => {
      const { data: Coffee, error1 } = await supabase.from("Coffee").select();
      if (error1) {
        alert(error1);
      }
      const cajas = [];
      Coffee.map((element) => {
        cajas.push(element.caja);
      });

      //Eliminar los repetido
      setobjetosDif([...new Set(cajas)]);
    };
    fetchBebidas();
  }, []);

  return (
    <div className="slidegroup">
      <aside className="menu-bar-ext">
        <h1>Categorias</h1>
        <div class="menu2">
          {objetosDif.map((obj) => (
            <a onClick={handleDrag} href={"#" + obj}>
              {obj}
            </a>
          ))}
        </div>
      </aside>
    </div>
  );
}
