import { supabase } from "../../../supabase/supabase";
import { useState, useEffect } from "react";

export default function Cuerpo({ objeto, enviarCart, restarCart }) {
  const [product, setProduct] = useState(objeto);
  useEffect(() => {
    setProduct(objeto);
  }, [objeto]);

  function mayus(aaa) {
    return aaa.charAt(0).toUpperCase() + aaa.slice(1);
  }
  const name = mayus(objeto[0].caja);

  const EditDatabaseCount = async (table, id, val) => {
    const { data: Coffee } = await supabase
      .from(table)
      .select("*")
      .eq("id", id);

    // Update the row in our database
    const { data, error } = await supabase
      .from(table)
      .update({ count: val })
      .eq("id", id)
      .select("*");
    if (error) console.log(error);
  };
  const handleClick3 = (e) => {
    const id =
      e.target.parentNode.parentNode.parentNode.parentNode.parentNode.id;

    const nArray = objeto.filter((objeto) => objeto.id == id);
    let val = nArray[0].count + 1;
    EditDatabaseCount("Coffee", id, val);

    setProduct((prevItems) =>
      prevItems.map((item) => {
        if (item.id == id) {
          return { ...item, count: val };
        } else return item;
      })
    );
    console.log(product);
  };
  const addCart = (e) => {
    const id = e.target.parentNode.id;
    const nArray = objeto.filter((objeto) => objeto.title == id);
    enviarCart(nArray[0]);
    setProduct(objeto);
  };
  const subCart = (e) => {
    const id = e.target.parentNode.id;
    const nArray = objeto.filter((objeto) => objeto.title == id);
    restarCart(nArray[0]);
    setProduct(objeto);
  };
  return (
    <section class="container specials" id="sec1">
      <div class="naveg" id={name}>
        <h2>{name}</h2>
      </div>
      <div id="nav2" class="container-products">
        {product.map((objeto) => (
          <div data-aos="flip-down" class="card-product" id={objeto.id}>
            <div class="container-img">
              <img src={objeto.image} />
            </div>
            <div class="content-card-product">
              <div class="center">
                {objeto.agotado ? (
                  <div class="star">
                    {Array.from({ length: objeto.star }, (_, i) => (
                      <ion-icon name="star"></ion-icon>
                    ))}
                  </div>
                ) : (
                  <h1>Agotado</h1>
                )}
              </div>
              <div class="caja">
                <div className="space">
                  <h3>{objeto.title}</h3>
                  <button onClick={handleClick3} className="btn-heart">
                    <h2>{objeto.count}</h2>
                    <ion-icon name="heart"></ion-icon>
                  </button>
                </div>
                <div class="sabores">
                  <ul>
                    {objeto.descripcion.map((element) => (
                      <li>{element}</li>
                    ))}
                  </ul>
                </div>
              </div>
              <div class="space">
                <p class="price">${objeto.price}.00</p>
                {objeto.agotado ? (
                  <div
                    className="contCarrito"
                    id={objeto.title}
                    style={{ marginRight: "10px" }}
                  >
                    <>
                      <button
                        disabled={objeto.disable}
                        className="MinusPlus"
                        onClick={subCart}
                      >
                        -
                      </button>
                      <h4>{objeto.Cant}</h4>
                      <button className="MinusPlus" onClick={addCart}>
                        +
                      </button>
                    </>
                  </div>
                ) : (
                  <>
                    <h6>.</h6>
                  </>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
